import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
// import * as getters from './getters'
import tunes from './modules/tunes'
import navigation from './modules/navigation'

Vue.use(Vuex)

export default new Vuex.Store({
  actions,
  // getters,
  modules: {
    tunes,
    navigation
  }
})



